@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
/* * ---> Star applies to everything */

a {
  text-decoration: none; /* Removes Underline from the links*/
  color: lightgray;
}

body {
  font-family: "Noto Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282828;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}
.MatchDetailedCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailedCard .match-date,.MatchDetailedCard .match-venue,.MatchDetailedCard .match-result{margin-top:10px}.MatchDetailedCard .additional-detail{text-align:right}.MatchDetailedCard .additional-detail>p{margin-bottom:20px}
.MatchSmallCard{padding:10px;margin-bottom:20px}.match-result{margin-top:10px}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:flex-end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1rem}
.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}
.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;grid-row-gap:10px;row-gap:10px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}
.team-tile{height:170px;text-align:center;border:1px solid #fff;padding:20px}
.home-page .app-name{font-size:3rem;margin:30px auto}.home-page .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}
